<template>
	<div>
		<div id="main" style="width: 400px; height: 280px"></div>
	</div>
</template>

<script>
	//  echarts  echarts 
	import * as echarts from 'echarts/core';
	//  Chart
	import { BarChart } from 'echarts/charts';
	//  Component
	import {
		TitleComponent,
		TooltipComponent,
		GridComponent,
		DatasetComponent,
		TransformComponent,
	} from 'echarts/components';
	// 
	import { LabelLayout, UniversalTransition } from 'echarts/features';
	//  Canvas  CanvasRenderer  SVGRenderer 
	import { CanvasRenderer } from 'echarts/renderers';

	export default {
		mounted() {
			// 
			echarts.use([
				TitleComponent,
				TooltipComponent,
				GridComponent,
				DatasetComponent,
				TransformComponent,
				BarChart,
				LabelLayout,
				UniversalTransition,
				CanvasRenderer,
			]);
			// 
			var myChart = echarts.init(document.getElementById('main'));
			myChart.setOption({
				title: {
					text: '',
				},
				tooltip: {},
				xAxis: {
					data: ['', '', '', '', '', '', ''],
				},
				yAxis: {},
				series: [
					{
						name: '',
						type: 'bar',
						data: [45, 80, 96, 90, 100, 67, 88],
					},
				],
			});
		},
	};
</script>

<style lang="scss" scoped></style>
